<template>
    <section>
        <div>
            <loader :is-visible="isLoading"></loader>
            <LeftColumn />

            <div class="right-content" v-if="isShowList">
                <tg-list :showCheckBox="true" :IsShowAction="true" ModuleName="Company" SubModuleCode="EmailTemplate"
                    IdentityColumn="template_id" :ListData="EmailTemplateData" :HeaderData="filteredHeaders"
                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false"
                    SearchPosition="top" :SearchFields="topSearchFields" :RenderRowActionMethod="renderActions"
                    :ListDataCallBackFunction="FetchData" :IsFilterApplied="searchCondition ? true : false"
                    v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                    v-on:ActionButtonClick="actionButtonClick" v-on:ClearFilter="ClearFilter"
                    v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
                    <!-- SLOT GOES INSIDE -->
                    <template v-slot:slotdata="{ data }">
                        <template v-if="data.column.COLUMN_NAME == 'template_name'">
                            <div v-if="data.row.company_id != null">
                                <a @click="clickEventOfColumn(data.row, data.column)" class="clickable-template">
                                    {{ data.row.template_name }}
                                </a>
                            </div>
                            <div v-else>
                                {{ data.row.template_name }}
                            </div>
                        </template>
                        <template v-if="data.column.COLUMN_NAME == 'status_id'">
                            <listing-status :isfor="'COMPANYSETUP_UPDATE'"
                                v-if="isCRMShowStausDrop(data.row.status_id, 'COMPANYSETUP_UPDATE')" ref="updatestatus"
                                :dataid="data.row.template_id" data-controller="COMPANYSETUP_UPDATE"
                                :statuslist="StatusListForListing" :item="data.row.template_id" data-action="index"
                                module="Company" subModule="EmailTemplate"
                                :dataselected="data.row.status_id.value == 1001 ? 'Active' : 'Inactive'"
                                :title="data.row.status_id.value == 1001 ? 'Active' : 'Inactive'"
                                :callbackfunction="FetchData" :isselectedvaluestring="true"></listing-status>
                        </template>
                    </template>
                </tg-list>
            </div>
            <div class="right-content" v-else-if="showEmailTemplate">
                <AddEmailTemplate :EditItme="Item"></AddEmailTemplate>

            </div>
            <div class="right-content" v-else-if="ShowAddAdvancedEmailTemplate">
                <AddAdvancedEmailTemplate :EditItme="Item" :Template_id="Template_id" :IsHide="IsHide">
                </AddAdvancedEmailTemplate>
            </div>
            <!---modal popup-->
            <!-- <AddCompanySetup :recordId="recordId" :title="$t('CAManageEmailTemplate')" v-if="showEmailTemplate" v-on:close="showEmailTemplate=false;recordId=0" v-on:addReloadData="FetchData();showEmailTemplate=false;recordId=0" v-bind:moduleName="'EmailTemplate'" v-bind:emitName="'addReloadData'" v-bind:addApiUrl="'/EmailTemplate/add'" v-bind:subModule="'EmailTemplate'" /> -->
        </div>
        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="isEditReport">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header theme-primary partition-full">
                                <h4 class="modal-title">{{ $t('clonetemplate') }}</h4>
                                <button type="button" class="close" v-on:click="close">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>
                            </div>

                            <div class="modal-body">
                                <div class="col-md-12">
                                    <div class="listing">
                                        <input type="text" class="form-control" v-model="cloneTemName"
                                            placeholder="Template Name" required />
                                    </div>
                                </div>
                            </div>
                            <div class="modal-footer">
                                <button type="button" class="btn btn-success" @click="triggerEditReport">
                                    Save
                                </button>
                                <button type="button" class="btn btn-danger" @click="close">
                                    Cancel
                                </button>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </transition>
        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="isShowReport">
                    <div class="modal-dialog modal-lg"
                        style="max-width: 90vw; width: 70vw; max-height: 70vh; overflow-y: auto;">
                        <div class="modal-content" style="min-height: 70vh;">
                            <div class="modal-header theme-primary partition-full">
                                <h4 class="modal-title">{{ $t('EmailTemplate') }}</h4>

                                <button type="button" class="close"
                                    style="margin-top: 0px; margin-right: 0px; margin-left: 5px;" v-on:click="close">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>

                            </div>

                            <div class="modal-body">
                                <div class="col-md-12">
                                    <div class="listing">
                                        <div v-html="Emailcontent"
                                            style="border: 1px solid #ddd; padding: 10px; margin-top: 10px; min-height: 50vh; overflow-y: auto;">
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="modal-footer">
                                <button type="button" class="btn btn-danger" @click="close">
                                    Cancel
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

    </section>
</template>
<script>
import EmailTemplateDataService from "@/services/DataService";
import Schema from "@/views/Company/EmailTemplate/EmailTemplateSchema";
import AddCompanySetup from "@/components/Modals/AddCompanySetup.vue";
import LeftColumn from "../LeftColumn.vue";
import AddAdvancedEmailTemplate from "@/components/Modals/Addnew.vue"
import AddEmailTemplate from '@/components/Modals/Add.vue'
// import AddEmailTemplate from "../../../components/Modals/Addednew.vue";
export default {
    components: {
        LeftColumn,
        AddCompanySetup,
        AddAdvancedEmailTemplate,
        AddEmailTemplate
    },
    props: {
        sourcetype:
        {
            type: String,
            required: true
        }
    },
    data() {
        return {
            recordId: 0,
            showEmailTemplate: false,
            noRecord: false,
            isLoading: false,
            CompanyId: null,
            UserId: null,
            EmailTemplateData: [],
            TotalRecords: 0,
            listheaderbuttons: Schema.buttons,
            topSearchFields: Schema.topsearchSchema,
            ShowManageView: false,
            ShowAddCustomReportView: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            listActions: Schema.listActions,
            searchCondition: '',
            conditionForInstantSearch: "",
            SortBy: '',
            SortExp: '',
            showsharereportview: false,
            showemailreportview: false,
            showschedulereportview: false,
            ReportID: null,
            reportname: '',
            reportid: '',
            foldername: '',
            folderid: '',
            LeadStatusList: [],
            searchcondition: '',
            MODULENAME: 'Company',
            SUBMODULECODE: 'EmailTemplate',
            privilegeParams: null,
            sharetype: '',
            filteredHeaders: [],
            StatusListForListing: [
                {
                    STATUS: 'Active',
                    STATUS_ID: 1001,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#28a745"
                }, {
                    STATUS: 'Inactive',
                    STATUS_ID: 1002,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#d72435"
                }
            ],
            ShowAddAdvancedEmailTemplate: false,
            Template_id: 0,
            isShowList: true,
            Emailcontent: '',
            Item: [],
            IsHide: false,
            isEditReport: false,
            isShowReport: false,
            cloneTemName: '',
            _category_id: 0,
        }
    },
    async created() {
        this.userTypeId = this.GetUserInfo.UserType == 'CA' ? 1 : this.GetUserInfo.UserType == 'PM' ? 2 : this.GetUserInfo.UserType == 'NU' ? 3 : 4;
        var ths = this;
        ths.isLoading = true;
        ths.CompanyId = ths.GetUserInfo.CompanyId;
        ths.privilegeParams = `controller=EmailTemplate&action=getCompanySetupModulePrivilege`;
        await ths.FetchData();
        await ths.GetModuleList();
        ths.getStatuslistForListing("Dynamic_Reporting", "en");
        // ths.GetTemplateCategoryListForCA();
        // ths.GetTemplateAllCategoryList();

    },
    computed: {
        filteredUserData() {
            return this.vueObj.UserData.map((item) => {
                const filteredItem = { ...item };
                if (filteredItem.USER_EMP_ID === null || filteredItem.USER_EMP_ID === '') {
                    filteredItem.USER_EMP_ID = '--';
                }
                return filteredItem;
            });
        },

    },
    updated() {
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white') {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t("EmailTemplate");
            divElement[0].appendChild(spanElement);
        }
    },

    methods: {
        close() {

            this.isEditReport = false;
            this.isShowReport = false;
        },
        GetTemplateCategoryListForCA() {
            var vm = this;
            vm.isLoading = true;
            EmailTemplateDataService.GetTemplateCategoryListForCA().then((response) => {
                const data = JSON.parse(response.data);
                if (data != null) {
                    const options = data.map(item => ({
                        name: item.template_category_name,
                        value: item.module_id
                    }));
                    vm.$forceUpdate?.();
                    vm.isLoading = false;
                }
            });
        },
        GetModuleList: async function () {
            var vm = this;
            var url = `userTypeId=${vm.userTypeId}`
            await EmailTemplateDataService.GetModuleList(url).then(function (response) {
                const data = response.data.Data;
                if (data != null) {
                    const options = data.map(item => ({
                        name: item.module_name,
                        value: item.module_id
                    }));
                    Schema.topsearchSchema.forEach(schemaField => {
                        if (schemaField.fieldName === 'SearchByCategory') {
                            schemaField.listOptions = options;
                        }
                    });
                }

            });
            vm.isLoading = false;
        },
        GetTemplateAllCategoryList() {
            try {
                EmailTemplateDataService.GetTemplateCategoryListForCA().then((response) => {
                    const data = JSON.parse(response.data);
                    if (data != null) {
                        const options = data.map(item => ({
                            name: item.template_category_name,
                            value: item.template_category_id
                        }));
                        Schema.topsearchSchema.forEach(schemaField => {
                            if (schemaField.fieldName === 'SearchByCategory') {
                                schemaField.listOptions = options;
                            }
                        });
                    }
                })
            }
            catch (error) {
                alert(error);
            }
        },
        ClearFilter: function () {
            var vm = this;
            Schema.topsearchSchema.forEach(item => {
                item.value = '';
                item.isSearch = false;
            })
            vm.searchCondition = '';
            vm.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'View':
                        action.isVisible = true;
                        break;
                    case 'Edit':
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;

        },
        commonHeaderButtonClick: function (event) {
            console.log(event)
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "ActiveMultipleListing":
                    this.UpdateStatus("Active");
                    break;
                case "InactiveMultipleListing":
                    this.UpdateStatus("Inactive");
                    break;
                case "AddEmailTemplate":
                    this.AddEmailTemplate();
                    break;
                case "AddAdvancedEmailTemplate":
                    this.AddAdvancedEmailTemplate();
                    break;
            }
        },
        AddEmailTemplate() {
            var vm = this;
            vm.ShowAddAdvancedEmailTemplate = false;
            vm.isShowList = false;
            vm.showEmailTemplate = true;

        },
        AddAdvancedEmailTemplate() {
            var vm = this;
            vm.showEmailTemplate = false;
            vm.isShowList = false;
            vm.ShowAddAdvancedEmailTemplate = true;
        },
        triggerEditReport() {
            if (this.cloneData) {
                this.actionButtonClick('clonereport', this.cloneData);
            }
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'sharereport':
                    var vm = this;
                    EmailTemplateDataService.CheckPrivilege(vm.privilegeParams + 'ShareReport').then((response) => {
                        if (response.data) {
                            vm.sharetype = callbackfunction
                            vm.folderid = item.FOLDER_ID;
                            vm.ReportID = item.REPORT_ID;
                            vm.foldername = item.REPORTNAME;
                            vm.showsharereportview = true;
                            $("#modalshareview").modal('show');
                            setTimeout(function () {
                                $("#modalshareview").modal('show');
                                setTimeout(function () {
                                    vm.SetBootstraptoolTip();
                                }, 200);
                            }, 1000);
                        }
                        else {
                            vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                            return false;
                        }
                    });
                    break;
                case 'sendemail':
                    vm.sendemail(item.REPORT_ID, item.MODULENAME, item.SUBMODULECODE);
                    break;
                case 'clonereport':
                    vm.cloneData = item;
                    if (!vm.isEditReport) {
                        vm.cloneTemName = item.template_name;
                        vm.isEditReport = true;
                    }
                    else {
                        vm.clonereport(item);
                    }
                    break;
                case 'setschedule':
                    vm.setschedule(item.REPORT_ID);
                    break;
                case 'ViewReport':
                    vm.ViewReport(item);
                    break;
            }
        },

        clonereport: async function (data) {
            let vm = this;
            var objData = {
                TemplateId: data.template_id,
                TemplateName: vm.cloneTemName,
                TemplateContent: "",
                StatusId: 0,
                TemplateCategoryId: 0,
                TemplateSubCategoryId: 0,
                TemplateSubject: "",
                TemplateJson: "",
                TemplateType: true,
                Type: 'CLONE'
            }

            var formData = new FormData();
            formData.append("postString", JSON.stringify(objData));
            EmailTemplateDataService.AddAdvancedEmailTemplate(formData).then((response) => {
                let result;
                if (typeof response.data === 'string') {
                    try {
                        result = JSON.parse(response.data)[0];
                    } catch (e) {
                        console.error("Invalid JSON response", e);
                        vm.ShowAlert(vm.$t("UnexpectedError"), "error", vm.$t('Alert'));
                        return;
                    }
                    if (result.status_id === 1 && result.status_msg === 'added') {
                        vm.ShowAlert(vm.$t("AddEmailSuccess"), "success", vm.$t('Alert'));
                        window.location.reload();
                    } else if (result.status_id === 2 && result.status_msg === 'updated') {
                        vm.ShowAlert(vm.$t("UpdateEmail"), "success", vm.$t('Alert'));
                        window.location.reload();
                    } else if (result.status_id === 3 && result.status_msg === 'cloned') {
                        vm.ShowAlert(vm.$t("EmailCloneSuccess"), "success", vm.$t('Alert'));
                        window.location.reload();
                    } else if (result.status_id === -2 && result.status_msg === "TemplateNameExists") {
                        vm.ShowAlert(vm.$t("TemplateNameExists"), "warning", vm.$t('Alert'));
                    } else {
                        vm.ShowAlert(vm.$t("UnexpectedError"), "error", vm.$t('Alert'));
                    }
                } else {
                    console.warn("Unexpected response format", response.data);
                    vm.ShowAlert(vm.$t("UnexpectedError"), "error", vm.$t('Alert'));
                }
            });

        },
        async GetEmailTemplateContentbyId(id) {
            var vm = this;
            vm.isLoading = true;
            var url = `emailcontentId=${id}`

            await EmailTemplateDataService.GetEmailcontent(url).then((response) => {
                if (response.data != null || response.data != '') {
                    vm.Emailcontent = response.data[0].TEMPLATE_CONTENT;
                }
            });
            vm.isLoading = false;
        },
        ViewReport: async function (item) {
            var vm = this;
            vm.isLoading = true;
            await vm.GetEmailTemplateContentbyId(item.template_id);
            vm.isShowReport = true;
            vm.isLoading = false;

        },
        sortdata: async function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            await this.FetchData();
            $("#th-" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await this.FetchData();
        },
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }

            await this.FetchData();
        },
        ManageFolder: function () {
            var vm = this;
            EmailTemplateDataService.CheckPrivilege(vm.privilegeParams + 'FolderListing').then((response) => {
                if (response.data) {
                    vm.ShowManageView = true;
                    $("#modalManageView").modal('show');
                    setTimeout(function () {
                        $("#modalManageView").modal('show');
                        setTimeout(function () {
                            vm.SetBootstraptoolTip();
                        }, 200);
                    }, 1000);
                    vm.$refs.managefolder?.GetViewList();
                }
                else {
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        AddDesignation: function () {
            var vm = this;
            console.log(vm, "Add");
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "Company",
                        subModuleCode: "EmailTemplate",
                        refCode: "COMPANYSETUP_DELETE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    EmailTemplateDataService.CommonDeleteData(objectPostString).then(async function (response) {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple").addClass("disabled");
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('EmailTemplate')]), "success", true, vm.$t("Alert"));
                            vm.PageNumber = parseInt(vm.PageNumber) - 1;
                            if (vm.PageNumber <= 0) {
                                vm.PageNumber = 1;
                            }
                            await vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('EmailTemplate')]), "failure", true, vm.$t("Alert"));
                        }

                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }

        },
        UpdateStatus: async function (status) {
            var vm = this;
            if (status == 'Active') {
                status = 1001;
            } else {
                status = 1002;
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.listheaderbuttons[2].isdisabled = false;
                vm.listheaderbuttons[3].isdisabled = false;
                vm.listheaderbuttons[4].isdisabled = false;
                vm.confirmR(vm.$t("AreYouSureToUpdateStatus"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    // Send IDs as a comma-separated string
                    var postJSON = {
                        ID: val,
                        StatusId: status,
                        IsFor: "COMPANYSETUP_UPDATE",
                        Module: "Company",
                        Submodule: "EmailTemplate",
                    };

                    var objectPostString = JSON.stringify(postJSON);
                    console.log(objectPostString);
                    EmailTemplateDataService.CommonUpdateStatus(objectPostString).then(response => {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.listheaderbuttons[2].isdisabled = true;
                            vm.listheaderbuttons[3].isdisabled = true;
                            vm.listheaderbuttons[4].isdisabled = true;
                            vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                    });
                });
            }
        },
        getRequestParams: function (pageNumber, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["is_paged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        clickEventOfColumn: async function (row, column) {
            var vm = this;
            vm.isLoading = true;
            await vm.GetEmailTemplateContentbyId(row.template_id);
            row.template_content = vm.Emailcontent;
            if (column.COLUMN_NAME == 'template_name') {
                if (row.template_type == true) {
                    vm.Item = row;
                    vm.showEmailTemplate = true;
                    vm.ShowAddAdvancedEmailTemplate = false;
                    vm.isShowList = false;
                }
                else {
                    vm.Item = row;
                    vm.Template_id = row.template_id;
                    vm.ShowAddAdvancedEmailTemplate = true;
                    vm.showEmailTemplate = false;
                    vm.isShowList = false;
                }
            }

            if (column.COLUMN_NAME == 'USERNAME') {
                this.$router.push({ name: 'viewcustomreport', params: { ReportID: row.REPORT_ID, ACCESS: row.ACCESS } });
            }
            vm.isLoading = false;
        },
        FetchData: async function () {
            var vueObj = this;
            vueObj.isLoading = true;

            var url = `CompanyId=${vueObj.CompanyId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&searchQuery=${vueObj.searchCondition.trim()}&module=${'Company'}&subModule=${'EmailTemplate'}`;
            await EmailTemplateDataService.EmailTemplateListing(url)
                .then(function (response) {
                    vueObj.Headers = response.data.FieldSchema;
                    var resultData = response.data.Data;
                    const filteredColumnNames = ["template_name", "template_subject", "template_category_id", "module_id", "created_at", "created_by", "status_id"];
                    vueObj.filteredHeaders = vueObj.Headers.filter(obj => filteredColumnNames.includes(obj.COLUMN_NAME));
                    vueObj.filteredHeaders.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "template_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME === "status_id") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });

                    if (response.data.Data != "" && response.data.Data != null) {

                        if (response.data.Data.length > 0) {
                            vueObj.EmailTemplateData = response.data.Data;
                            vueObj.EmailTemplateData.forEach(item => {
                                item.isCheckBoxDisabled = false;
                                if (item.company_id == null) {
                                    item.isCheckBoxDisabled = true;
                                }
                                item.module_id = JSON.parse(item.module_id).Name != null ? JSON.parse(item.module_id).Name : "--";
                                item.template_category_id = JSON.parse(item.template_category_id).Name != null ? JSON.parse(item.template_category_id).Name : "--";
                                item.modified_by = JSON.parse(item.modified_by);
                                item.status_id = JSON.parse(item.status_id);
                                item.created_by = JSON.parse(item.created_by).Name != null ? JSON.parse(item.created_by).Name : "--";


                            });

                            vueObj.TotalRecords = vueObj.EmailTemplateData[0].total_records;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.listheaderbuttons[2].isdisabled = true;
                            vueObj.listheaderbuttons[3].isdisabled = true;
                            vueObj.listheaderbuttons[4].isdisabled = true;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.EmailTemplateData = [];
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                        }
                        if (localStorage.SavedParameters != undefined) {
                            localStorage.removeItem("SavedParameters");
                        }

                    }

                    else {
                        vueObj.noRecord = true;
                        vueObj.EmailTemplateData = [];
                        vueObj.TotalRecords = 0
                    }
                    vueObj.isLoading = false;


                });
        },
        closeModalSharePopup: function () {
            $("#modalshareview").modal('hide');
            this.showsharereportview = false;
            this.vm.Emailcontent = '';
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            EmailTemplateDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
    }
}
</script>

<style>
.modal-header {
    position: relative;
    padding-right: 40px;
}

.clickable-template {
    color: blue;
    text-decoration: underline;
    cursor: pointer;
}

.modal-header .close {
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 10;
    background: transparent;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
}
</style>
