<template>
  <div>
    <textarea :id="editorId"></textarea>
  </div>
</template>

<script>
export default {
  name: 'TinyMCEEditor',
  props: {
    editorId: {
      type: String,
      default: 'editor',
    },
    value: {
      type: String,
      default: '',
    },
    updateContent: {
      type: Function,
      required: false,
    },
  },
  mounted() {
    if (window.tinymce) {
      this.initTinyMCE();
    } else {
      const script = document.createElement('script');
      script.src = 'https://cdnjs.cloudflare.com/ajax/libs/tinymce/5.2.2/tinymce.min.js';
      script.onload = () => this.initTinyMCE();
      document.head.appendChild(script);
    }
  },
  methods: {
    initTinyMCE() {
      window.tinymce.init({
        selector: `#${this.editorId}`,
        height: 500,
        resize: false,
        menubar: true,
        automatic_uploads: true,
        plugins: 'advlist anchor autolink charmap fullscreen link lists preview save table  wordcount code image',
        toolbar: 'undo redo | bold italic underline | alignleft aligncenter alignright  | bullist numlist | code | link image table | preview fullscreen',
        file_picker_types: 'image',
        setup: (editor) => {
          editor.on('change', () => {
            this.syncContentWithParent(editor);
          });
        },
        file_picker_callback: (callback, value, meta) => {
          if (meta.filetype === 'image') {
            const input = document.createElement('input');
            input.type = 'file';
            input.accept = 'image/*';
            input.addEventListener('change', (e) => {
              const file = e.target.files[0];
              const reader = new FileReader();
              reader.onload = (event) => {
                const id = 'blobid' + Date.now();
                const blobCache = tinymce.activeEditor.editorUpload.blobCache;
                const base64 = event.target.result.split(',')[1];
                const blobInfo = blobCache.create(id, file, base64);
                blobCache.add(blobInfo);
                callback(blobInfo.blobUri(), { title: file.name });
              };
              reader.readAsDataURL(file);
            });
            input.click();
          }
        },
        init_instance_callback: (editor) => {
          editor.setContent(this.value);
        },
      });
    },

    syncContentWithParent(editor) {
      const content = editor.getContent();
      if (typeof this.updateContent === 'function') {
        this.updateContent(content);
      }
    },

    // Method to print only the content of the editor without scrollbars or UI
    printEditorContent() {
      debugger
      const editor = tinymce.get(this.editorId);
      if (!editor) return;

      const content = this.value;
      const printWindow = window.open('', '', 'height=800,width=1000');

      printWindow.document.write(`
        <html>
          <head>
            <title>Print</title>
            <style>
              body {
                margin: 20px;
                font-family: sans-serif;
              }
              img {
                max-width: 100%;
              }
              table {
                width: 100%;
                border-collapse: collapse;
              }
              table, th, td {
                border: 1px solid black;
              }
                body{
                width:80%;
                heigth:81%;
                }
            </style>
          </head>
          <body>${content}</body>
        </html>
      `);

      printWindow.document.close();
      printWindow.focus();

      printWindow.onload = () => {
        printWindow.print();
        printWindow.close();
      };
    },
  },

  watch: {
    value(newValue) {
      const editor = window.tinymce.get(this.editorId);
      if (editor && newValue !== editor.getContent()) {
        editor.setContent(newValue);
      }
    },
  },
};
</script>
