<template>
    <section class="">
        <loader :is-visible="isLoading"></loader>
        <div class="container-fluid mt-2 p-0">
            <div class="w-100">
                <div class="col-md-12 p-0">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ emailTemplateLabel }}</span>

                        <span class="p-actions float-right tablehead">
                            <a class="p-action-btn text-white" href="/EmailTemplate/Index">
                                <i class="fa fa-angle-double-left"></i><br>{{ $t('BacktoList') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <dynamic-form :schema="FormSchema" ref="AddEditEmail" :buttons="buttons" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-emailEditor="{ data }">
                    <div id="app">
                        <EmailEditor ref="emailEditor" name="emailEditor" :appearance="appearance"
                            :min-height="minHeight" :project-id="Template_id" :locale="locale" :tools="tools"
                            :options="options" v-on:load="editorLoaded">
                        </EmailEditor>
                        <span v-if="!flag" name="emailEditor" class="text-danger ml-4">{{ data.error }}</span>
                    </div>
                </template>
            </dynamic-form>
        </div>
    </section>
</template>

<script>
import {
    EmailEditor
} from 'vue-email-editor';
import DataService from '@/services/DataService';
export default {
    props: {
        Template_id: {
            type: Number,
            required: true
        },
        EditItme: {
            type: Array,
            required: false
        },
        IsHide: {
            type: Boolean,
            required: false
        }
    },
    name: 'app',
    components: {
        EmailEditor,
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                    astype: "TextField",
                    label: this.$t('TemplateName'),
                    name: "TemplateName",
                    value: "",
                    visibility: true,
                    placeholder: "Template Name",
                    validationRules: {
                        "required": true
                    },
                    disabled: false,
                },
                {
                    astype: "TextField",
                    label: this.$t('Subject'),
                    name: "Subject",
                    value: "",
                    visibility: true,
                    placeholder: "Subject",
                    validationRules: {
                        "required": true
                    },
                },
                {
                    astype: "SelectField",
                    label: this.$t('Category'),
                    name: "Category",
                    value: "",
                    visibility: true,
                    placeholder: "Category",
                    config: {
                        options: [],
                        onChange: this.GetSubCategoryDDL
                    },
                    validationRules: {
                        "required": true
                    },
                },
                {
                    astype: "SelectField",
                    label: this.$t('SubCategory'),
                    name: "SubCategory",
                    visibility: true,
                    value: "",
                    placeholder: "Sub Category",
                    config: {
                        options: [],
                    },
                    validationRules: {
                        "required": true
                    },

                },

                {
                    astype: "SelectField",
                    label: this.$t('Status'),
                    name: "Status",
                    value: "",
                    visibility: true,
                    placeholder: "Status",
                    config: {
                        options: [],
                    },
                },

                ]
            },
            {
                layoutType: "single",
                Data: [{
                    astype: "SlotField",
                    name: "emailEditor",
                    value: "",
                    placeholder: "Create Template",
                    validationRules: { "required": false },
                    config: {
                        options: [],
                        onChange: this.PaymentGatewayAdditionalDetail
                    },

                },]
            }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.BackButton,
                },
            ],
            minHeight: '800px',
            locale: 'en',
            tools: {
                // disable image tool
                image: {
                    enabled: true,
                },
            },
            options: {},
            appearance: {
                theme: 'light',
                panels: {
                    tools: {
                        dock: 'right',
                        show: false
                    },
                },
            },
            CatData: [],
            TempContent: '',
            isDataLoaded: false,
            flag: false,
            SubCatData: [],
            fileData: [],
            UserId: '',
            CompanyId: '',
            ContainerName: ''
        }
    },
    computed: {
        emailTemplateLabel() {
            return this.EditItme.template_id > 0
                ? this.$t('EditEmailTemplate')
                : this.$t('AddEmailTemplate');
        }
    },
    async created() {
        var vm = this;
        this.userTypeId = this.GetUserInfo.UserType == 'CA' ? 1 : this.GetUserInfo.UserType == 'PM' ? 2 : this.GetUserInfo.UserType == 'NU' ? 3 : 4;
        if (vm.IsHide) {

            (vm.FormSchema)[0].Data.forEach(element => {
                element.visibility = false;
            });
        }
        vm.ContainerName = vm.GetUserInfo.ContainerName;
        vm.UserId = vm.GetUserInfo.ID,
            vm.CompanyId = vm.GetUserInfo.CompanyId,
            await vm.GetModuleList();
        // await vm.GetTemplateCategoryListForCA()
        await vm.GetStatusList();
        if (vm.EditItme.template_id > 0) {
            await vm.BindTemplateDetails(vm.EditItme.template_id);
            vm.CatData.forEach(option => {
                if (option.module_name === vm.EditItme.module_id) {
                    vm.FormSchema[0].Data[2].value = option.module_id;
                }
            });
            if (vm.FormSchema[0].Data[2].value != undefined && vm.FormSchema[0].Data[2].value != "") {
                await vm.GetSubCategoryDDL((vm.FormSchema)[0].Data[2].value);
            }
            vm.Template_id = vm.EditItme.template_id;
            (vm.FormSchema)[0].Data[0].value = vm.EditItme.template_name;
            (vm.FormSchema)[0].Data[0].disabled = true;
            (vm.FormSchema)[0].Data[1].value = vm.EditItme.template_subject;
            (vm.FormSchema)[0].Data[4].value = vm.EditItme.status_id.value;
            vm.editorData = vm.EditItme.template_content;
            if (vm.SubCatData.length > 0) {
                vm.SubCatData.forEach(option => {
                    if (option.NAME === vm.EditItme.template_category_id) {
                        (vm.FormSchema)[0].Data[3].value = option.SUB_MODULE_ID;
                        vm.$refs.AddEditEmail.UpdateKeyValue();
                    }
                });
            }
        }
    },
    updated() {
        var vm = this;
        try {
            this.$refs.emailEditor.editor.registerCallback('image', function (file, done) {
                let files = file.attachments[0]
                let reader = new FileReader();
                let imageBase64;
                reader.onloadend = function () {
                    var Prefixlength = reader.result.lastIndexOf(',');
                    imageBase64 = reader.result.substring(Prefixlength + 1, reader.result.length);
                    var n = files.name.lastIndexOf('.');
                    var extensionName = files.name.substring(n + 1, files.name.length);
                    var attachemnetMime = '{"ContentType":"' + files.type + '","Size":"' + files.size + '","Extension":"' + extensionName + '"}';
                    var fileObj = {
                        attachement_mime: attachemnetMime,
                        fileName: files.name,
                        base64String: imageBase64,
                        fileSize: files.size,
                        contentType: files.type,
                        Extension: extensionName,
                        subModuleCode: 'EmailTemplate',
                        moduleName: 'EmailTemplate',
                        containerName: vm.ContainerName,
                        userId: vm.UserId,
                        companyId: vm.CompanyId,
                        id: 0
                    };
                    vm.fileData.push(fileObj);
                }

                if (file) {
                    reader.readAsDataURL(files);
                }

            });
        } catch (ex) { }

    },
    methods: {

        async UploadFile(id) {
            var vm = this;
            vm.isLoading = true;
            vm.fileData.forEach((item) => {
                item.id = id;
            })
            var PostString = JSON.stringify(vm.fileData);
            await DataService.UploadFile(PostString).then((response) => {
                if (response.data.length > 0) {
                    if (response.data[0].Status == 'Success') {
                        response.data[0].Name;
                        vm.isLoading = false;
                    } else {
                        vm.isLoading = false;
                    }
                }
            });
            vm.isLoading = false;
        },

        async BindTemplateDetails(Template_id) {
            var vm = this;
            if (Template_id > 0) {
                vm.isLoading = true;
                var url = `id=${Template_id}`
                await DataService.GetEmailTemplateDetails(url).then((response) => {
                    var data = response.data.templateContent;
                    var parsedJSON = JSON.parse(response.data.templateJson);
                    var striJSON = response.data.templateJson;
                    vm.editorLoaded(data, parsedJSON, striJSON)
                    vm.$refs.emailEditor.editor.loadDesign({ striJSON });
                    vm.$refs.emailEditor.editor.exportHtml((data) => { });
                    vm.$refs.emailEditor.loadDesign(vm.TempContent);
                    vm.isDataLoaded = true;
                })
            } else {
                vm.isLoading = false;
            }

        },
        //get ststus list ddl
        async GetStatusList() {
            var vm = this;
            vm.isLoading = true;
            await DataService.GetStatusList().then((response) => {
                if (JSON.parse(response.data) != null) {
                    var SubCatData = [];
                    SubCatData = JSON.parse(response.data);
                    SubCatData.forEach((itm, index) => {
                        if (itm.STATUS != 'Deleted') {
                            (vm.FormSchema)[0].Data[4].config.options.push({
                                name: itm.STATUS,
                                value: itm.STATUS_ID
                            })
                        }
                    })
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
        },
        //get sub category ddl list by module id
        async GetSubCategoryDDL(evt) {
            var vm = this;
            vm.isLoading = true;
            var url = `ModuleID=${0}`
            if (evt && typeof evt === 'object' && 'target' in evt && evt.target?.value !== '') {
                url = `ModuleID=${evt.target.value}`
            }
            else {
                url = `ModuleID=${evt}`
            }
            await DataService.GetTemplateSubCategoryList(url).then((response) => {
                if (JSON.parse(response.data) != null) {
                    vm.SubCatData = [];
                    vm.SubCatData = JSON.parse(response.data);
                    (vm.FormSchema)[0].Data[3].config.options = [];
                    vm.SubCatData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[3].config.options.push({
                            name: itm.NAME,
                            value: itm.SUB_MODULE_ID
                        })
                    })
                    vm.$refs.AddEditEmail.UpdateKeyValue();
                    vm.isLoading = false;
                } else {
                    vm.isLoading = false;
                }
                (vm.FormSchema)[0].Data[3].value = '';
            });
        },
        //get category ddl list
        GetModuleList: async function () {
            var vm = this;
            var url = `userTypeId=${vm.userTypeId}`
            await DataService.GetModuleList(url).then(function (response) {
                if (response.data.Data.length > 0) {
                    vm.CatData = response.data.Data;
                    vm.CatData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[2].config.options.push({
                            name: itm.module_name,
                            value: itm.module_id,
                        })
                    })
                }
            });
            vm.$refs.AddEditEmail.UpdateKeyValue();
            vm.isLoading = false;
        },
        // async GetTemplateCategoryListForCA() {
        //     debugger
        //     var vm = this;
        //     vm.isLoading = true;
        //     await DataService.GetTemplateCategoryListForCA().then((response) => {
        //         if (JSON.parse(response.data) != null) {
        //             vm.CatData = [];
        //             vm.CatData = JSON.parse(response.data);
        //             debugger
        //             vm.CatData.forEach((itm, index) => {
        //                 (vm.FormSchema)[0].Data[2].config.options.push({
        //                     name: itm.template_category_name,
        //                     value: itm.template_category_id,
        //                 })
        //             })
        //             vm.$refs.AddEditEmail.UpdateKeyValue();
        //             vm.isLoading = false;
        //         }
        //         vm.isLoading = false;
        //     });
        // },

        //get additional columns show/hide on payment gateway code change
        async PaymentGatewayAdditionalDetail() {
            var vm = this;
            vm.isLoading = true;
            var url = `ModuleID=${evt.target.value}`
            await DataService.GetTemplateSubCategoryList(url).then((response) => {
                if (JSON.parse(response.data) != null) {
                    vm.SubCatData = [];
                    vm.SubCatData = JSON.parse(response.data);
                    vm.SubCatData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[3].config.options.push({
                            name: itm.NAME,
                            value: itm.SUB_MODULE_ID
                        })
                    })
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
        },
        // called when the editor is created
        async editorLoaded() {
            var vm = this;
            vm.isLoading = true;
            if (vm.Template_id > 0) {
                var url = `id=${vm.Template_id}`
                await DataService.GetEmailTemplateDetails(url).then((response) => {
                    debugger
                    vm.TemplateData = response.data;
                    if (response.data != null) {
                        var parsedJSON = JSON.parse(response.data.templateJson);
                        vm.$refs.emailEditor.editor.loadDesign(parsedJSON);
                        vm.FormSchema[0].Data[0].disabled = true;
                        vm.FormSchema[0].Data[0].value = vm.TemplateData.templateName;
                        vm.FormSchema[0].Data[1].value = vm.TemplateData.templateSubject;
                        // vm.FormSchema[0].Data[3].value = vm.TemplateData.templateSubCategoryId;
                        vm.FormSchema[0].Data[4].value = vm.TemplateData.statusId;
                        var foundModuleObj = vm.CatData.find(x => x.template_category_id == vm.TemplateData.templateCategoryId);
                        if (foundModuleObj) {
                            vm.FormSchema[0].Data[2].value = foundModuleObj.module_id;
                            vm.GetSubCategoryDDL(vm.FormSchema[0].Data[2].value)
                        }
                        vm.isLoading = false;
                    }
                })
            } else {
                vm.isLoading = false;
            }

        },
        // submit the forma data and email template data
        onSubmit(e) {
            var vm = this;
            vm.isLoading = true;
            var Template_Name = vm.FormSchema[0].Data[0].value;
            var Template_Subject = vm.FormSchema[0].Data[1].value;
            var moduleId = vm.FormSchema[0].Data[2].value;
            var Template_SubCategory_Id = vm.FormSchema[0].Data[3].value;
            var Status_Id = vm.FormSchema[0].Data[4].value;
            vm.$refs.emailEditor.editor.exportHtml((data) => {
                var json = data.design;
                var html = data.html;
                var content = JSON.stringify(json);
                vm.flag = false;
                (vm.FormSchema)[1].Data[0].validationRules.required = false;
                var strippedHtml = vm.stripHtmlTags(html);
                if (strippedHtml.trim().length >= 2) {
                    vm.flag = true;
                    (vm.FormSchema)[1].Data[0].validationRules.required = false;
                } else {
                    vm.flag = false;
                    (vm.FormSchema)[1].Data[0].validationRules.required = true;
                }
                var objData = {
                    TemplateId: (vm.EditItme.template_id > 0 ? vm.EditItme.template_id : vm.Template_id),
                    TemplateName: Template_Name,
                    TemplateContent: html,
                    StatusId: Status_Id,
                    TemplateCategoryId: moduleId,
                    TemplateSubCategoryId: Template_SubCategory_Id,
                    TemplateSubject: Template_Subject,
                    TemplateJson: content,
                    TemplateType: false
                }
                var formData = new FormData();
                var postString = { "postString": objData }
                formData.append("postString", JSON.stringify(objData));
                // var url= `TemplateId=${vm.TemplateId}&TemplateName=${Template_Name}&TemplateContent=${html}&StatusId=${Status_Id}&TemplateCategoryId=${Template_Category_Id}&TemplateSubCategoryId=${Template_SubCategory_Id}&TemplateSubject=${Template_Subject}&TemplateJson=${content}&TemplateType=true`
                // var param = `objTemp=${JSON.stringify(objData)}`;
                if (vm.flag) {
                    DataService.AddAdvancedEmailTemplate(formData).then(async (response) => {
                        const Data = JSON.parse(response.data);
                        if (Data[0].status == '' && Data[0].msg == "TemplateNameExists") {
                            vm.ShowAlert(vm.$t("TemplateNameExists"), "warning", vm.$t('Alert'));
                            vm.isLoading = false;
                        }
                        else {

                            if (Data[0].status_id == 1 && Data[0].status_msg == 'added') {
                                await vm.UploadFile(Data[0].template_id);
                                vm.ShowAlert(vm.$t("AddEmailSuccess"), "success", vm.$t('Alert'));
                                vm.isLoading = false;
                                window.location.reload();
                            }
                            else if (Data[0].status_id == 2 && Data[0].status_msg == 'updated') {
                                await vm.UploadFile(vm.EditItme.template_id);
                                vm.ShowAlert(vm.$t("UpdateEmailTemplate"), "success", vm.$t('Alert'));
                                // vm.isLoading = false;
                                window.location.reload();
                            }
                            else {
                                vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                                vm.isLoading = false;
                            }
                        }

                    })
                }

            });

        },
        stripHtmlTags: function (html) {
            if (!html) return '';
            var strippedHtml = html.replace(/<style([\s\S]*?)<\/style>/gi, '');
            strippedHtml = strippedHtml.replace(/<[^>]*>?/gm, '').replace(/\n/g, '');
            return strippedHtml;
        },
        BackButton() {
            window.location.reload();
        },
    },
};
</script>

<style>
.unlayer-editor iframe {
    position: absolute;
}
</style>
