                if (option.module_name === vm.EditItme.module_id) {
                    vm.FormSchema[0].Data[2].value = option.module_id;
                }
            });
            if (vm.FormSchema[0].Data[2].value != undefined && vm.FormSchema[0].Data[2].value != "") {
                await vm.GetSubCategoryDDL((vm.FormSchema)[0].Data[2].value);
                vm.SubCatData.forEach(option => {
                    if (option.NAME === vm.EditItme.template_category_id) {
