        GetModuleList: async function () {
            var vm = this;
            var url = `userTypeId=${vm.userTypeId}`
            await DataService.GetModuleList(url).then(function (response) {
                if (response.data.Data.length > 0) {
                    vm.CatData = response.data.Data;
                    vm.CatData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[2].config.options.push({
                            name: itm.module_name,
                            value: itm.module_id,
                        })
                    })
                }
            });
            vm.$refs.dynamicform.UpdateKeyValue();
            vm.isLoading = false;
        },
        async GetStatusList() {
            var vm = this;
            vm.isLoading = true;
            await DataService.GetStatusList().then((response) => {
                if (JSON.parse(response.data) != null) {
                    vm.SubCatData = [];
                    vm.SubCatData = JSON.parse(response.data);
                    vm.SubCatData.forEach((itm, index) => {
                        if (itm.STATUS != 'Deleted') {
                            (vm.FormSchema)[0].Data[4].config.options.push({
                                name: itm.STATUS,
                                value: itm.STATUS_ID
                            })
                        }
                    })
                    vm.$refs.dynamicform.UpdateKeyValue();
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
        },
        //get sub category ddl list by module id
        async GetEmailTemplateColumn(evt) {
            var vm = this;
            vm.isLoading = true;
            var url = `moduleId=${vm.SetModuleId}&submoduleId=${evt.target.value}`;
            var getcolumn = evt.target.value == '' || evt.target.value == null ? 0 : evt.target.value;
            try {
                const response = await DataService.GetEmailTemplateColumnList(url);
                if (response.data) {
                    const parsedArray = JSON.parse(response.data);
                    if (parsedArray.length > 0 && parsedArray[0].MAIN_DATA) {
                        const mainData = JSON.parse(parsedArray[0].MAIN_DATA);
                        if (mainData.DATA && Array.isArray(mainData.DATA)) {
                            vm.UserGuidData = mainData.DATA.map(element => {
                                return {
                                    ...element,
                                    name: element.name
                                        .split('_')
                                        .map(word => word.charAt(0).toUpperCase() + word.slice(1))
                                        .join(' ')
                                };
                            });

                        } else {
                            vm.UserGuidData = [];
                        }
                    } else {
                        vm.UserGuidData = [];
                    }
                } else {
                    vm.UserGuidData = [];
                }
            } catch (error) {
                console.error("Error parsing email template column data:", error);
                vm.UserGuidData = [];
            } finally {
                vm.isLoading = false;
            }
        },
        async GetSubCategoryDDL(evt, data) {
            var vm = this;
            vm.isLoading = true;
            var url = `ModuleID=${0}`
            if (evt && typeof evt === 'object' && 'target' in evt && evt.target?.value !== '') {
                url = `ModuleID=${evt.target.value}`
            }
            else {
                url = `ModuleID=${evt}`
            }
            var moduleId = parseInt(url.split('=')[1]);
            await DataService.GetTemplateSubCategoryList(url).then((response) => {
                if (JSON.parse(response.data) != null) {
                    vm.SubCatData = [];
                    vm.SubCatData = JSON.parse(response.data);
