                            value: itm.SUB_MODULE_ID
                        })
                    })

                } else {
                    vm.isLoading = false;

                }
                vm.SetModuleId = moduleId;
                vm.SetTemplateVariable(moduleId);
                (vm.FormSchema)[0].Data[3].value = '';
                vm.$refs.dynamicform.UpdateKeyValue();
                vm.isLoading = false;
            });
